import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import CssBaseline from '@material-ui/core/CssBaseline';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import Slide from '@material-ui/core/Slide';
import { Button, Grid, IconButton, Menu, MenuItem, List, Divider, Drawer, Hidden, ListItem, ListItemIcon, Tooltip } from '@material-ui/core';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

const drawerWidth = 300;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing(3),
  },
}));

export default function NavigationBar(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor="left"
        style={{ width: '100%' }}
      >
        <Typography variant="h3" align="left" style={{ fontWeight: 800, opacity: 0.2, marginLeft: 10, marginTop: 20, marginBottom: 10 }}>Abu's Portfolio</Typography>
        <Divider />
        <List>
          <ListItem>
            <Button onClick={props.scrollToAbout} style={{ opacity: 0.5, fontWeight: 600 }}>About</Button>
          </ListItem>
          <ListItem>
            <Button onClick={props.scrollToResearchProjects}>Research Projects</Button>
          </ListItem>
          <ListItem>
            <Button onClick={props.scrollToDesignProjects}>Design Projects</Button>
          </ListItem>
          <ListItem>
            <Button onClick={props.scrollToCommunityProjects}>Community Projects</Button>
          </ListItem>
          <ListItem>
            <Button onClick={props.scrollToMusic}>Music</Button>
          </ListItem>
          <ListItem>
            <Button onClick={props.scrollToResume}>CV / Resume</Button>
          </ListItem>
          <ListItem>
            <Button disabled={true}>Contact</Button>
          </ListItem>
          <ListItem style={{ marginLeft: 20 }}>
            <Tooltip title="abubatjargal@gmail.com">
            <IconButton href="mailto:name@email.com">
              <MailOutlineIcon />
            </IconButton>
            </Tooltip>
            <br />
            <br />
            <Tooltip title="LinkedIn">
            <IconButton target="_blank" href="https://www.linkedin.com/in/ariunbold-batjargal/">
              <LinkedInIcon />
            </IconButton>
            </Tooltip>
          </ListItem>
        </List>
      </Drawer>
    </div>
  );
}

